import React, { useEffect, useState } from "react";
import { getImages } from "../../util/util";

export default function Search() {
  var [data, setData] = useState<any>({});

  var [query, setQuery] = useState<string>("");
  var [type, setType] = useState<string>("");

  var [isAllowed, setAllowed] = useState<boolean>(false);
  var [isLoading, setLoading] = useState<boolean>(true);

  function getData() {
    setLoading(true);
    getImages(encodeURI(query), type)
      .then(({ data }) => {
        setData(data.collection);
        console.log(data.collection);
        setLoading(false);
      })
      .catch((e) => {
        throw e;
      });
  }

  const onchangeHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
    setQuery(e.target.value);
    if (query.length > 1) setAllowed(false);
    else setAllowed(true);
  };

  useEffect(() => {
    setType("image");
    setLoading(false);
    console.log(data.items);
  }, []);
  return !isLoading ? (
    <div>
      <div className="field has-addons">
        <p className="control is-expanded">
          <input
            className={`input is-link ${isAllowed ? "is-danger" : ""}`}
            type="text"
            placeholder="Link input"
            onChange={onchangeHandler}
          ></input>
        </p>
        <p className="control">
          {!isAllowed ? (
            <button className="button is-link" onClick={getData}>
              Submit
            </button>
          ) : null}
        </p>
      </div>
      <div className="space-search-images">
        {data.items.map((el: any) => (
          <span key={el.data[0].nasa_id}>
            <img
              src={el.links[0].href}
              alt=""
              style={{ height: "20vh", margin: "1vh 0.5vw" }}
            />
          </span>
        ))}
      </div>
    </div>
  ) : (
    <div>
      <button
        className="button is-loading"
        style={{ width: "100vw", height: "100vh" }}
      >
        Loading
      </button>
    </div>
  );
}
