import React from "react";
import { useParams } from "react-router";
import { arr } from "../../util/MarsRoverImages";

interface RouteParams {
  page: string;
}

export default function MarsRovers() {
  const { page } = useParams<RouteParams>();

  if (parseInt(page) > 9)
    window.location.href = "/LxrsV1-Astronomy/#/space/mars/rover/1";

  var array = arr.slice(parseInt(page) * 85, parseInt(page) * 85 + 85);
  console.log(parseInt(page) * 85, parseInt(page) * 85 + 85);

  return (
    <div className="mars-rovers-container">
      {array.map((photo) => (
        <div key={photo.id}>
          <span>
            <img src={photo.img_src} style={{ width: "15vw" }}></img>
          </span>
          <span>
            <h1>Rover:</h1>
            <p>
              Name (id): {photo.rover.name} ({photo.rover.id})
            </p>
            <p>Status: {photo.rover.status}</p>
          </span>
          <span>
            <h1>Camera:</h1>
            <p>
              Short Name (id): {photo.camera.name} ({photo.camera.id})
            </p>
            <p>Full Name: {photo.camera.full_name}</p>
          </span>
          <span>
            <h1>Other:</h1>
            <p>Date on Earth: {photo.earth_date}</p>
            <p>id: {photo.id}</p>
          </span>
        </div>
      ))}
      <nav className="pagination" role="navigation" aria-label="pagination">
        <a className="pagination-previous is-left">Previous</a>
        <ul className="pagination-list is-centered">
          <li>
            <a
              className="pagination-link"
              href="/LxrsV1-Astronomy/#/space/mars/rover/1"
            >
              1
            </a>
            <a
              className="pagination-link"
              href="/LxrsV1-Astronomy/#/space/mars/rover/2"
            >
              2
            </a>
            <a
              className="pagination-link"
              href="/LxrsV1-Astronomy/#/space/mars/rover/3"
            >
              3
            </a>
            <a
              className="pagination-link"
              href="/LxrsV1-Astronomy/#/space/mars/rover/4"
            >
              4
            </a>
            <a
              className="pagination-link"
              href="/LxrsV1-Astronomy/#/space/mars/rover/5"
            >
              5
            </a>
            <a
              className="pagination-link"
              href="/LxrsV1-Astronomy/#/space/mars/rover/6"
            >
              6
            </a>
            <a
              className="pagination-link"
              href="/LxrsV1-Astronomy/#/space/mars/rover/7"
            >
              7
            </a>
            <a
              className="pagination-link"
              href="/LxrsV1-Astronomy/#/space/mars/rover/8"
            >
              8
            </a>
            <a
              className="pagination-link"
              href="/LxrsV1-Astronomy/#/space/mars/rover/9"
            >
              9
            </a>
          </li>
        </ul>
        <a className="pagination-next is-right">Next page</a>
      </nav>
    </div>
  );
}
