import APOD from "./Pages/space/APOD";
import Mars from "./Pages/space/Mars";
import "./App.css";
import { NavBar } from "./Components";
import { HashRouter as Router, Route } from "react-router-dom";
import { Switch } from "react-router";
import LandingPage from "./Pages/Landing";
import MarsRovers from "./Pages/space/MarsRovers";
import React from "react";
import Search from "./Pages/space/Search";
import MainSpace from "./Pages/space/Main";
import NPM from "./Pages/Coding/npm";

function App() {
  return (
    <>
      <NavBar />
      <Router basename="/">
        <Switch>
          <Route path="/" exact component={LandingPage} />

          <Route path="/space/" exact component={MainSpace} />
          <Route path="/space/apod" exact component={APOD} />
          <Route path="/space/mars" exact component={Mars} />
          <Route path="/space/library/search" exact component={Search} />
          <Route path="/space/mars/rover/:page" exact component={MarsRovers} />

          <Route path="/coding/npm" exact component={NPM} />
        </Switch>
      </Router>
    </>
  );
}

export default App;
