import React from "react";

interface Props {
  text: string;
  width: string;
  cmd?: boolean;
}

export default function CodeBlock(props: Props) {
  return (
    <div
      className={`codeblock ${props.cmd ? "command-block" : ""}`}
      style={{ width: props.width, padding: "2vh 0" }}
    >
      <pre>
        {props.cmd ? "$ " : ""}
        {props.text}
      </pre>
    </div>
  );
}
