import React, { useState } from "react";
import CodeBlock from "../../Components/CodeBlock";
import Footer from "../../Components/Footer";
import {
  Discord,
  InstallPackageNPM,
  InstallPackageYARN,
} from "../../util/CodeBlocks";

export default function NPM() {
  var [language, setLanguage] = useState("javascript");
  var [installer, setInstaller] = useState("npm");

  function HandleChangeInstaller() {
    setInstaller(installer === "npm" ? "yarn" : "npm");
    console.log(installer);
  }
  function HandleChangeLanguage() {
    setLanguage(language === "javascript" ? "typescript" : "javascript");
    console.log(language);
  }

  return (
    <>
      <div
        className="npm-page"
        style={{ background: "rgb(28, 29, 33) none repeat scroll 0% 0%;" }}
      >
        <div className="npm-page-config">
          <button className="is-link button" onClick={HandleChangeInstaller}>
            Change CLI tool
          </button>
          <button className="is-link button" onClick={HandleChangeLanguage}>
            Change language
          </button>
        </div>
        <div>
          <h1 id="install">Install the package</h1>
          <p>You can change the documentation settings above.</p>
          <CodeBlock
            text={installer === "npm" ? InstallPackageNPM : InstallPackageYARN}
            width="50vw"
            cmd
          />
        </div>
        <div>
          <h1 id="discord">Discord</h1>
          <p>This part of the npm package is not done yet.</p>
          <p>All features showcased here are working.</p>
          <br />
          <p>Import the discord package:</p>
          <CodeBlock
            text={
              language === "javascript"
                ? Discord.javascript.import
                : Discord.typescript.import
            }
            width="50vw"
          />
          <br />
          <br />
          <p>
            If you want multiple folders inside your command directory
            (recommended to keep your file system clean):
          </p>
          <CodeBlock
            text={
              language === "javascript"
                ? Discord.javascript.advancedCMDdir
                : Discord.typescript.advancedCMDdir
            }
            width="50vw"
          />
          <p>If you like to keep all your command files in one folder:</p>
          <CodeBlock
            text={
              language === "javascript"
                ? Discord.javascript.notAdvancedCMDdir
                : Discord.typescript.notAdvancedCMDdir
            }
            width="50vw"
          />
        </div>
      </div>
      <Footer />
    </>
  );
}
