export var InstallPackageNPM = "npm i lxrsv1";
export var InstallPackageYARN = "yarn add lxrsv1";

export var Discord = {
  typescript: {
    import: 'import discord from "lxrsv1/discord";',
    advancedCMDdir: `discord.setCommandsDirNotAdvanced<[string, string][]>('<your commands folder>').forEach((file) => {
    // do stuff with the files
});`,
    notAdvancedCMDdir: `discord.setCommandsDirAdvanced<string[]>('<your commands folder>').forEach((file) => {
    // do stuff with the files
});`,
  },
  javascript: {
    import: 'const discord = require("lxrsv1/discord");',
    advancedCMDdir: `discord.setCommandsDirNotAdvanced('<your commands folder>').forEach((file) => {
    // do stuff with the files
});`,
    notAdvancedCMDdir: `discord.setCommandsDirAdvanced('<your commands folder>').forEach((file) => {
    // do stuff with the files
});`,
  },
};
