import React, { useEffect, useState } from "react";
import Footer from "../../Components/Footer";
import { getAPOD } from "../../util/util";

export default function APOD() {
  const [data, setData] = useState({
    copyright: "",
    date: "",
    explanation: "",
    media_type: "",
    service_version: "",
    title: "",
    url: "",
  });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    getAPOD()
      .then(({ data }) => {
        setData(data);
        console.log(data);
        setLoading(false);
      })
      .catch(console.error);
  }, []);
  return !loading ? (
    <>
      <div className="apod-container">
        {data.media_type === "video" ? (
          <iframe
            src={data.url}
            allowFullScreen
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            title="Video"
          />
        ) : (
          <img src={data.url} alt="" />
        )}
        <h3>{data.explanation}</h3>
      </div>
      <br />
      <br />
      <hr />
      <br />
      <br />
      <div className="apod-container-2">
        <details>
          <summary>More information</summary>
          <p>
            <u>Copyright</u>: {data.copyright}
          </p>
          <p>
            <u>Date</u>: {data.date}
          </p>
          <p>
            <u>Explenation</u>: {data.explanation}
          </p>
          <p>
            <u>Media Type</u>: {data.media_type}
          </p>
          <p>
            <u>Service Version</u>: {data.service_version}
          </p>
          <p>
            <u>Title</u>: {data.title}
          </p>
          <p>
            <u>URL</u>: {data.url}
          </p>
          <p>
            <u>Data from</u>: https://api.nasa.gov
          </p>
        </details>
      </div>
      <Footer />
    </>
  ) : (
    <div>
      <button
        className="button is-loading"
        style={{ width: "100vw", height: "100vh" }}
      >
        Loading
      </button>
    </div>
  );
}
